<template>
  <div id="c1" style="width:49%;height:450px;display:inline-block"></div>
</template>

<script>
import china from "@/common/json/china.json";
export default {
  props: {
      data:Array
  },  
  data() {
    return {
      chart: null,
      ds:null,
      chinaMap:null,
      china,
    };
  },
  mounted() {
    this.renderMapChart();
    this.mapDataSettle(this.data);
  },
  watch:{
    data(val){
      this.mapDataSettle(val);
    }
  },
  methods: {
    mapDataSettle:function(data){
      // 
      const userDv = this.ds
        .createView()
        .source(data)
        .transform({
          geoDataView: this.chinaMap, //geo
          field: "province", //
          type: "geo.region", //
          as: ["longitude", "latitude"] //longitudelatitude
        });

      const userView= this.chart.view(); //this.chartviewuserView
      userView.source(userDv)
      userView
        .polygon()
        .tooltip("province*pv*uv")
        .color("pv", "#0050B3-#1890FF-#40A9FF-#69C0FF-#BAE7FF")
        .position("longitude*latitude");
      this.chart.render();
    },
    renderMapChart: function() {
      this.chart= new G2.Chart({
        container: "c1",
        forceFit: true,
        height: 450
      });
      this.chart.tooltip({
        showTitle: false
      });
      //
      this.chart.scale({
        longitude: {
          sync: true
        },
        latitude: {
          sync: true
        }
      });
      //
      this.chart.axis(false);


      // 
      this.ds = new DataSet();
      //geo
      this.chinaMap = this.ds.createView("back").source(china, {
        type: "GeoJSON"
      });
      //view
      const chinaView= this.chart.view();
      //
      chinaView.source(this.chinaMap);
      //
      chinaView.tooltip(false);
      chinaView
        .polygon()  //
        .position("longitude*latitude") //
        .style({
          fill: "#fff", //
          stroke: "#ccc", //
          lineWidth: 1 //
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

</style>


